@import "./../../scss/mixins";
.verificationform {
  &-container {
    padding: 3rem;
    &-cards {
      display: grid;
      grid-template-columns: auto auto auto auto;
      gap: 2.5rem;
    }
  }
}

.verification_application_info_container{
  width:25rem;
  @include respond(phone) {
    width: 18rem;
}
  height:7rem;
  margin-bottom: 2rem;
  margin-left: 2rem;
  background-color:  #D9D9D9;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
padding-left: 3rem;
border-radius: 2rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  &_image{
      width:6rem;
      height:6rem;
      padding:1.2rem;
      background-color: transparent;
  }
  &_heading{
      padding-top: 2.5rem;
      font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 1.2rem;
color: #808080;
  }
}
.verification_info_main_container{
  margin-left: 3rem;
  p{
    font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 2rem;
color: #000000;
opacity: 0.33;
  }
}
.verification_application_info_heading{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 2rem;
color: #000000;
opacity: 0.33;
margin-left: 4rem;
}
// Sub components

.formscard {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 26px;
  &-container {
    padding: 2rem 2.5rem;
    display: flex;
    align-items: center;
    &-logo {
      & > img {
        height: 60px;
        margin-right: 2rem;
      }
    }
    &-content {
      & > p {
        font-size: 1.6rem;
      }
    }
  }
}

.user {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 26px;
  &-container {
    padding: 2rem 2.5rem;
    display: flex;
    align-items: center;
    &-logo {
      & > img {
        height: 60px;
        margin-right: 2rem;
      }
    }
    &-content {
      & > p {
        font-size: 1.6rem;
      }
    }
  }
}